import React from 'react';
import { Row } from 'antd';
// import the core library.
import ReactEchartsCore from 'echarts-for-react/lib/core';

// then import echarts modules those you have used manually.
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/pie';
// import 'echarts/lib/chart/scatter';
// import 'echarts/lib/chart/radar';

// import 'echarts/lib/chart/map';
// import 'echarts/lib/chart/treemap';
// import 'echarts/lib/chart/graph';
// import 'echarts/lib/chart/gauge';
// import 'echarts/lib/chart/funnel';
// import 'echarts/lib/chart/parallel';
// import 'echarts/lib/chart/sankey';
// import 'echarts/lib/chart/boxplot';
// import 'echarts/lib/chart/candlestick';
// import 'echarts/lib/chart/effectScatter';
// import 'echarts/lib/chart/lines';
// import 'echarts/lib/chart/heatmap';

// import 'echarts/lib/component/graphic';
// import 'echarts/lib/component/grid';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/tooltip';
// import 'echarts/lib/component/polar';
// import 'echarts/lib/component/geo';
// import 'echarts/lib/component/parallel';
// import 'echarts/lib/component/singleAxis';
// import 'echarts/lib/component/brush';

import 'echarts/lib/component/title';

// import 'echarts/lib/component/dataZoom';
// import 'echarts/lib/component/visualMap';

// import 'echarts/lib/component/markPoint';
// import 'echarts/lib/component/markLine';
// import 'echarts/lib/component/markArea';

// import 'echarts/lib/component/timeline';
import 'echarts/lib/component/toolbox';
// import { Rect } from 'echarts/lib/util/graphic';

// import 'zrender/lib/vml/vml';

const titleConfig = {
  left: 'center',
  top: 0,
  textStyle: {
    color: '#000000'
  }
};
const toolboxConfig = {
  feature: {
    saveAsImage: { show: true }
  },
  right: 40
};
const legendConfig = {
  left: 40
};
const styles = {
  margin: '40px 0',
  height: '500px'
};
const tooltipConifg = {
  trigger: 'axis',
  axisPointer: {
    type: 'cross',
    crossStyle: {
      color: '#999'
    }
  }
};


function CommonCharts(props) {
  return (
    <div>
      <Row>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: props.title.title1,
              ...titleConfig
            },
            tooltip: tooltipConifg,
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.xAxis,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '累计受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '累计受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.totalAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.overallCloseRateData
              }
            ]
          }}
        />
      </Row>

      <Row>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: props.title.title2,
              ...titleConfig
            },
            tooltip: tooltipConifg,
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.xAxis,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '新增受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '新增受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.newAddedAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.graphData.newAddedCloseRateData
              }
            ]
          }}
        />
      </Row>

      <Row>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: props.title.title5,
              ...titleConfig
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            toolbox: toolboxConfig,
            series: [
              {
                name: '剩余未关',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)',
                  color: '#000'
                },
                data: props.graphData.openData
              }
            ]
          }}
        />
      </Row>

      <Row>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: props.title.title3,
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            legend: {
              data: ['需处理受理', '及时关闭率', '总体关闭率'],
              ...legendConfig
            },
            xAxis: [
              {
                type: 'category',
                data: props.xAxis,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '需处理受理'
              },
              {
                type: 'value',
                name: '比率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '需处理受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.needHandleAcceptData
              },
              {
                name: '及时关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.graphData.needHandleCloseOnTimeRateData
              },
              {
                name: '总体关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.graphData.needHandleCloseRateData
              }
            ]
          }}
        />
      </Row>

      <Row style={{ margin: '40px 0' }}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: props.title.title4,
              ...titleConfig
            },
            tooltip: tooltipConifg,
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.xAxis,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '处理时长（天）'
              },
            ],
            legend: {
              data: ['已关闭', '剩余未关'],
              ...legendConfig
            },
            series: [
              {
                name: '已关闭',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.closeAverageHandleTimeData
              },
              {
                name: '剩余未关',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.graphData.openAverageHandleTimeData
              }
            ]
          }}
        />
      </Row>
    </div>
  );
}

export default CommonCharts;
