import {
  ADD_CATEGORY,
  ADD_CATEGORIES
} from '../action-types';

export default (state = [], action) => {
  switch (action.type) {
    case ADD_CATEGORY:
      const { category } = action.payload;
      return [
        ...state,
        category
      ];
    case ADD_CATEGORIES:
      const { categories } = action.payload;
      return [
        ...state,
        ...categories
      ]
    default:
      return state;
  }
}
