  TICKET_STATUS,
  CATEGORIES,
  STATISTIC_ITEMS,
  TICKET_ACCEPT,
  TICKET_OPEN,
  TICKET_PAUSE,
  TICKET_INVALID,
  TICKET_CLOSE,
