import React from 'react';
import { Row, Col, Form, Select, Button } from 'antd';
import ProjectTable from './ProjectTable';
import {
  STATISTIC_PERIOD_ADD,
  STATISTIC_BEFORE_PERIOD_REMAIN,
  STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_THIS_YEAR_TOTAL,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_BEFORE_PERIOD_NEED_HANDLE,
  STATISTIC_BEFORE_PERIOD_TOTAL,
  STATISTIC_PERIOD_RATE,
  CATEGORIES,
  KEY_PROJECT_NAME,
  KEY_STATISTIC_ITEM,
  KEY_TICKET_TOTAL,
  STATISTIC_ITEMS
} from '../constants';
import {
  formatToTable,
  formatToRateTable
} from '../utils';
import XLSX from 'xlsx';
import moment from 'moment';

class ProjectView extends React.Component {
  static defaultProps = {
    data: {},
    projects: [],
    items: STATISTIC_ITEMS,
    name: 'statistic'
  }

  constructor(props) {
    super(props);
    this.state = {
      filteredProjects: [],
      filteredItems: [],
      loading: false
    }
    this.exportData = this.exportData.bind(this);
  }

  filterProjects = (projects) => {
    this.setState({
      filteredProjects: projects.length > 0 ? projects : []
    })
  }

  filterItems = (items) => {
    this.setState({
      filteredItems: items.length > 0 ? items : []
    });
  }

  exportData() {
    this.setState({
      loading: true
    });
    let header = [KEY_PROJECT_NAME, KEY_STATISTIC_ITEM, ...CATEGORIES, KEY_TICKET_TOTAL];
    let sheet1 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.periodNewAddedResult), {
      header: header
    });
    let sheet2 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.thisYearBeforePeriodResult), {
      header: header
    });
    let sheet3 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.thisYearBeforePeriodTotalResult), {
      header: header
    });
    let sheet4 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.thisYearTotalResult), {
      header: header
    });
    let sheet5 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.lastYearBeforePeriodResult), {
      header: header
    });
    let sheet6 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.lastYearBeforePeriodTotalResult), {
      header: header
    });
    let sheet7 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.beforePeriodNeedHandleResult), {
      header: header
    });
    let sheet8 = XLSX.utils.json_to_sheet(formatToTable(this.props.data.periodTotalResult), {
      header: header
    });
    let sheet9 = XLSX.utils.json_to_sheet(formatToRateTable(this.props.data.rateResult), {
      header: header
    });
    let workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, sheet1, STATISTIC_PERIOD_ADD);
    XLSX.utils.book_append_sheet(workbook, sheet2, STATISTIC_BEFORE_PERIOD_REMAIN);
    XLSX.utils.book_append_sheet(workbook, sheet3, STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL);
    XLSX.utils.book_append_sheet(workbook, sheet4, STATISTIC_THIS_YEAR_TOTAL);
    XLSX.utils.book_append_sheet(workbook, sheet5, STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN);
    XLSX.utils.book_append_sheet(workbook, sheet6, STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL);
    XLSX.utils.book_append_sheet(workbook, sheet7, STATISTIC_BEFORE_PERIOD_NEED_HANDLE);
    XLSX.utils.book_append_sheet(workbook, sheet8, STATISTIC_BEFORE_PERIOD_TOTAL);
    XLSX.utils.book_append_sheet(workbook, sheet9, STATISTIC_PERIOD_RATE)
    XLSX.writeFile(workbook, this.props.name + '-' + moment(Date.now()).format('YYYY-MM-DD-HH-MM-SS') + '.xlsx')
    this.setState({
      loading: false
    })
  }

  render() {
    return (
      <div>
        <Form>
          <Row>
            <Col span={16}>
              <Form.Item
                label='请选择要过滤的项目'
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  mode="multiple"
                  style={{ width: '100%' }}
                  onChange={this.filterProjects}
                >
                  {
                    this.props.projects.map((project) => (
                      <Select.Option key={project}>{project}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
            <Col span={8}>
              <Button loading={this.state.loading} onClick={this.exportData}>导出数据</Button>
            </Col>
          </Row>
          <Row>
            <Col span={16}>
              <Form.Item
                label="请选择要过滤的统计项目"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  mode="multiple"
                  style={{ width: '100%' }}
                  onChange={this.filterItems}
                >
                  {
                    this.props.items.map((item) => (
                      <Select.Option key={item}>{item}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_ADD}
              data={formatToTable(this.props.data.periodNewAddedResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_NEED_HANDLE}
              data={formatToTable(this.props.data.beforePeriodNeedHandleResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.props.data.thisYearBeforePeriodResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.props.data.thisYearBeforePeriodTotalResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_THIS_YEAR_TOTAL}
              data={formatToTable(this.props.data.thisYearTotalResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.props.data.lastYearBeforePeriodResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.props.data.lastYearBeforePeriodTotalResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>

          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_TOTAL}
              data={formatToTable(this.props.data.periodTotalResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_RATE}
              data={formatToRateTable(this.props.data.rateResult)}
              filteredProjects={this.state.filteredProjects}
              filteredItems={this.state.filteredItems}
            />
          </div>
        </Row>
      </div>
    );
  }
}

export default ProjectView;
