import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Form, Upload, Button, DatePicker } from 'antd';

class DataForm extends React.Component {
  static defaultPorps = {
    onStart: () => {}
  }
  static propTypes = {
    onStart: PropTypes.func
  }

  constructor(props) {
    super(props);
    this.state = {
      fileList: []
    };
    this.startStatistic = this.startStatistic.bind(this);
  }

  validateForm() {
    return new Promise((resolve, reject) => {
      this.props.form.validateFields((err, values) => {
        if (!err) {
          resolve();
        } else {
          reject();
        }
      })
    });
  }

  startStatistic() {
    this.validateForm().then(() => {
      const form = this.props.form;
      let startTime = new Date(form.getFieldValue('startDate').set({ hour: 0, minute: 0, second: 0 }));
      let endTime = new Date(form.getFieldValue('endDate').set({ hour: 23, minute: 59, second: 59 }));
      this.props.onStart(this.state.fileList, startTime, endTime);
    }).catch(() => {});
  }

  compareToStartDate = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value < form.getFieldValue('startDate')) {
      callback('结束时间应大于开始时间!');
    } else {
      callback();
    }
  }

  compareToEndDate = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value > form.getFieldValue('endDate')) {
      callback('开始时间应小于结束时间!');
    } else {
      callback();
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form layout="horizontal">
        <Row>
          <Col span={12}>
            <Form.Item
              label="请选择文件"
              labelCol={{ span: 8 }}
              wrapperCol={{ span: 16 }}
            >
              {
                getFieldDecorator('files', {
                  rules: [{ required: true, message: '请选择要统计的文件!' }]
                })(
                  <Upload
                    accept=".xls,xlsx"
                    multiple
                    fileList={this.state.fileList}
                    beforeUpload={(file) => {
                      this.setState(({ fileList }) => {
                        return { fileList : [...fileList, file] };
                      });
                      return false;
                    }}
                    onRemove={(file) => {
                      this.setState(({ fileList }) => {
                        const index = fileList.indexOf(file);
                        const newFileList = fileList.slice();
                        newFileList.splice(index, 1);
                        return {
                          fileList: newFileList
                        };
                      });
                    }}
                  >
                    <Button>支持文件格式: .xls, .xlsx</Button>
                  </Upload>
                )
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={8}>
            <Form.Item
              label='统计周期开始时间'
              labelCol={{ span: 12 }}
              wrapperCol={{ span: 12 }}
            >
              {
                getFieldDecorator('startDate', {
                  rules: [
                    { required: true, message: '请选择统计周期开始时间' },
                    { validator: this.compareToEndDate }
                  ]
                })(<DatePicker />)
              }
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item
              label="统计周期结束时间"
              labelCol={{ span: 12 }}
              wrapperCol={{ span: 12 }}
            >
              {
                getFieldDecorator('endDate', {
                  rules: [
                    { required: true, message: '请选择统计周期结束时间' },
                    { validator: this.compareToStartDate }
                  ]
                })(<DatePicker />)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col offset={4}>
            <Button loading={this.props.loading} type="primary" onClick={this.startStatistic}>
              {
                this.props.loading ? '正在统计...': '开始统计'
              }
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrapDataForm = Form.create()(DataForm);

export default WrapDataForm;
